import React, { useState, useRef } from 'react';

import './css/styles.css';

function Question({ title, content }) {
	const contentBox = useRef();
	const [isFolded, setIsFolded] = useState(false);

	const showContent = () => {
		const content = contentBox.current;
		content.style.height = content.scrollHeight + 'px';
	};

	const hideContent = () => {
		const content = contentBox.current;
		content.style.height = null;
	};

	const handleFold = () => {
		setIsFolded(!isFolded);
		!isFolded ? showContent() : hideContent();
	};

	return (
		<div className="question">
			<div className="header" onClick={handleFold}>
				<h3>{title}</h3>
				<span className="material-icons-outlined">{isFolded ? 'expand_less' : 'expand_more'}</span>
			</div>
			<div className="content" ref={contentBox} dangerouslySetInnerHTML={{ __html: content }}></div>
		</div>
	);
}

export default Question;
